import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

function getMenuItem(name, link){
  return (
    <Link
    to={link}
    style={{
      color: `white`,
      textDecoration: `none`,
    }}
  >
    {name}
  </Link>
  )
}

function getSeparator(){
  return (<span style={{'margin-left':'10px','margin-right':'10px'}}></span>)
}

const Header = ({ siteTitle }) => (
  <header
    style={{
      background: '#64c37d', //`rebeccapurple`,
      marginBottom: `1.45rem`,
    }}
  >
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `1.45rem 1.0875rem`,
      }}
    >
      <h1 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            color: `white`,
            textDecoration: `none`,
          }}
        >
          {siteTitle}
        </Link>
      </h1>
         <div style={{'margin-top':'10px'}}>
{getMenuItem('home', '/')}
{getSeparator()}
{getMenuItem('about', '/about')}
</div>
    </div>
    <div>

    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
