import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

function getProject(title, description, link) {
  return (
    <li>
      <p>
        <strong>{title}</strong>
      </p>
      <p>
        {description}
        <br/>
        {link && <p><a href={link} target='_blank' rel="noopener noreferrer">here</a></p>}
      </p>
    </li>
  )
}

const IndexPage = () => (
  <Layout>
    <SEO title="Home" keywords={[`gatsby`, `application`, `react`]} />
    <p>My journey as a Datanaut</p>
    <ul>
      {getProject(
        'Searchnauts',
        "NASA Datanauts' countries and cities around the world.",
        'https://nasadatanauts.github.io/alexbelloni/searchnauts/')}
      {getProject(
        'Hubbot',
        "Chatbot that searches for NASA APIs, according to your key word.")}
      {getProject(
        'My Water-Cooler Meeting',
        "Speech about APIs by using my track 1.",
        'https://drive.google.com/open?id=0Bz94QYIr9g9FZDZ0R1QwREdfWXM')}
      {getProject(
        'Track 2: Orbital Dynamics',
        `Contribution into that track (tasks 3 and 4 on 'Future Work' list).
          My GMATUtil tool helps when doing Dan O'Neil's tutorial, "Web Visualization of a Trajectory Generated from the General Mission Analysis Tool", part 1.
          That track creates a library of web-based resources for mission visualizations.`,
        'https://1drv.ms/u/s!ApuCsfBpp_GZgpZuHC3XaDTdkjOc9Q')}
      {getProject(
        'Track 1: Intro to Coding', 
        `Tutorial: Make an API (lesson 2).`,
        'https://nasadatanauts.github.io/alexbelloni/track1lesson2/index.html')}
    </ul>
    {/* <p>Now go build something great.</p>
    <div style={{ maxWidth: `300px`, marginBottom: `1.45rem` }}>
      <Image />
    </div>
    <Link to="/page-2/">Go to page 2</Link> */}
  </Layout>
)

export default IndexPage
